
export default {

  init() {

    window.captchaLogCallback = function (response) {
      console.log('captcha Login callback ');
      grecaptcha.reset(window.reCaptcha.captchaLog);
    };

    window.captchaOnLoadCallback = function () {
      console.log('Капча загружена');
      // window.captcha.
      let capId = grecaptcha.render('loginButtonSubmit', {
        'sitekey': window.appSettings.reCaptchaSiteKey,
        'data-size': 'invisible',
        'callback': window.captchaLogCallback
      });
      window.reCaptcha['captchaLog'] = capId;
    };
  }
};



